﻿/*
 *  Boilerplate Mixins
 *  Last Update: 1/30/2017
*/

.text-hide() { text-indent:-9999px; white-space:nowrap; outline:none;}

.box-sizing(@sizing-model) { 
    -webkit-box-sizing:@sizing-model; 
            box-sizing:@sizing-model; 
}

.box-shadow(...) { 
    -webkit-box-shadow:@arguments; 
            box-shadow:@arguments; 
}

.transform(...) {
     -webkit-transform: @arguments; 
        -moz-transform: @arguments; 
         -ms-transform: @arguments; 
          -o-transform: @arguments; 
             transform: @arguments; 
}

.transition (@args: all 0.2s ease-out) {
	-webkit-transition: @args;
	   -moz-transition: @args;
	     -o-transition: @args;
	        transition: @args;
}

.vertical-center(@position: relative) {
    position: @position; 
    top: 50%; 
    .transform( translateY(-50%)); 
}

.text-columns(@columns: 2, @width: auto, @gap: normal, @rule-style: none, @rule-width: 0.0625rem, @rule-color: #ededed ) {

    -webkit-columns: @columns @width;
       -moz-columns: @columns @width;
            columns: @columns @width; 

    -webkit-column-gap: @gap;
       -moz-column-gap: @gap;
            column-gap: @gap; 

    -webkit-column-rule: @rule-style @rule-width @rule-color;
       -moz-column-rule: @rule-style @rule-width @rule-color;
            column-rule: @rule-style @rule-width @rule-color; 
}

.linear-gradient(@start-color, @end-color, @start-percent: 0%, @end-percent: 100%) {
    background: @start-color; 
    background: -webkit-linear-gradient(top, @start-color @start-percent, @end-color @end-percent) repeat-x; 
    background:         linear-gradient(to bottom, @start-color @start-percent, @end-color @end-percent) repeat-x; 
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)",argb(@start-color),argb(@end-color))); 
}

.radial-gradient(@start-color, @end-color) {
    background: @start-color; 
    background: -webkit-radial-gradient(circle, @start-color, @end-color); 
    background:    -moz-radial-gradient(circle, @start-color, @end-color); 
    background:      -o-radial-gradient(circle, @start-color, @end-color); 
    background:         radial-gradient(circle, @start-color, @end-color); 
}

.arrow-up(@size:0.25rem, @color:#000) { 
    width: 0; 
    height: 0; 
    border-left: @size solid transparent; 
    border-right: @size solid transparent; 
    border-bottom: @size solid @color; 
    border-top: none;
}

.arrow-down(@size:0.25rem, @color:#000) { 
    width: 0; 
    height: 0; 
    border-left: @size solid transparent; 
    border-right: @size solid transparent; 
    border-top: @size solid @color; 
    border-bottom: none;
}